
#preview {
  margin-left: -.5rem;
}
pre {
  margin-left: 0;
  border-left: solid 1px rgba(0,0,0,.125);
  padding: 0 0 14px 14px;
  -moz-tab-size: 2;
    -o-tab-size: 2;
       tab-size: 2;
  font-size: 0.8rem;
}
.focused {
  margin-left: -14px;
  padding-left: 11px;
  border-left: solid 3px rgb(2, 117, 216);
  border-top: dotted .1px rgba(2, 117, 216, .50);
  border-bottom: dotted .1px rgba(2, 117, 216, .50);
}



